<template>
  <div id="Nation">
    <el-divider content-position="left"
      ><p class="title">疫情数据</p></el-divider
    >
    <el-row :gutter="20">
      <el-col :span="12">
        <el-card id="map-card" shadow="never">
          <div id="nationMap"></div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card id="table-card" shadow="never">
          <el-table
            :data="nationTable"
            style="width: 100%;"
            border
            id="nation-table"
            :cell-style="{ 'vertical-align': 'top' }"
          >
            <el-table-column prop="name" label="统计类型"></el-table-column>
            <el-table-column prop="value" label="统计人数"></el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <div id="nationSunBurst" v-show="false"></div>
    <template>
      <el-carousel :interval="4000" type="card" height="200px">
        <el-carousel-item v-for="(item, index) in this.carousel" :key="index">
          <p class="medium" v-for="(text, i) in item" :key="i">{{ text }}</p>
        </el-carousel-item>
      </el-carousel>
    </template>
    <el-divider content-position="left"
      ><p class="title">疫情政策</p></el-divider
    >
    <div id="nationLine"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
import 'echarts/map/js/china.js'

export default {
  name: 'Nation',
  data() {
    return {
      carousel: [],
      nationTable: [],
      nationMapChart: '',
      nationSunBurstChart: '',
      nationLineChart: '',
      startTime: 0,
      mapOption: {
        title: {
          text: '全国最新疫情地图',
        },
        series: {
          name: '确诊人数',
          type: 'map',
          map: 'china',
          label: {
            show: true,
            color: '#333',
            fontSize: 10,
          },
          itemStyle: {
            areaColor: '#eee',
          },
          roam: false,
          zoom: 1.2,
          emphasis: {
            label: {
              color: '#fff',
              fontSize: 12,
            },
            itemStyle: {
              areaColor: '#ccc',
            },
          },
          data: [],
        },
        visualMap: [
          {
            type: 'piecewise',
            show: true,
            pieces: [
              { min: 10000 },
              { min: 1000, max: 9999 },
              { min: 100, max: 999 },
              { min: 10, max: 99 },
              { min: 1, max: 9 },
              { max: 0 },
            ],
            inRange: {
              symbol: 'rect',
              color: [
                '#FFD318',
                '#FFAA85',
                '#FF7B69',
                '#CC2929',
                '#8C0D0D',
                '#660208',
              ],
            },
            tooltip: {
              trigger: 'item',
            },
          },
        ],
      },
    }
  },
  watch: {
    '$store.state.mainModule.shouldTotalDataShown': function() {
      if (this.$store.state.mainModule.shouldTotalDataShown == true) {
        this.drawNationTable()
      }
    },
    '$store.state.mainModule.shouldNationDataShown': function() {
      if (this.$store.state.mainModule.shouldNationDataShown == true) {
        this.drawNationMap('nationMap')

        //this.drawNationLine('nationLine')
        this.carousel = this.$store.state.mainModule.carouselData
        //this.$store.commit('mainModule/resetFlag')
      }
    },
  },
  methods: {
    drawNationMap(id) {
      console.log('draw map')
      for (var i = 0; i < this.$store.state.mainModule.provinceCount; i++) {
        this.mapOption.series.data.push({
          name: this.$store.state.mainModule.nationData[i].provinceShortName,
          value: this.$store.state.mainModule.nationData[i].confirmedCount,
        })
      }
      this.nationMapChart = echarts.init(document.getElementById(id))
      this.nationMapChart.setOption(this.mapOption)
    },
    drawNationTable() {
      this.nationTable = [
        {
          name: '现存确诊病例',
          value: this.$store.state.mainModule.chinaData.currentConfirmedCount,
        },
        {
          name: '累计确诊病例',
          value: this.$store.state.mainModule.chinaData.confirmedCount,
        },
        {
          name: '累计疑似病例',
          value: this.$store.state.mainModule.chinaData.suspectedCount,
        },
        {
          name: '累计治愈病例',
          value: this.$store.state.mainModule.chinaData.curedCount,
        },
        {
          name: '累计死亡病例',
          value: this.$store.state.mainModule.chinaData.deadCount,
        },
      ]
      //console.log(this.$store.state.mainModule.chinaData)
    },
    drawNationLine(id) {
      console.log('draw line')
      this.lineOption.xAxis.data = this.$store.state.mainModule.xAxis
      this.lineOption.series[0].data = this.$store.state.mainModule.curedLine
      this.lineOption.series[1].data = this.$store.state.mainModule.deadLine
      this.nationLineChart = echarts.init(document.getElementById(id))
      this.nationLineChart.setOption(this.lineOption)
    },
  },
  created() {
    console.log('create Nation')
    this.startTime = new Date().getTime()
    this.$store.dispatch('mainModule/getInstantNationData')
  },
  mounted() {},
  destroyed() {
    var time = new Date().getTime()
    this.$store.dispatch('mainModule/recordCountTime', 'nation', time)
  },
}
</script>

<style>
.title {
  font-size: 20px;
}
.el-divider {
  margin: 35px 0 35px 0;
}
#map-card {
  width: 100%;
  height: 600px;
}
#table-card {
  width: 100%;
  height: 600px;
}
#nation-table {
  margin: 155px 0 0 0;
}
#nationMap {
  width: 100%;
  height: 600px;
}
#nationSunBurst {
  width: 100%;
  height: 1500px;
}
.el-carousel {
  width: 100%;
}
.el-carousel__item p {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 25px;
  margin: 25px 0 0 0;
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>
